<template>
  <div class="page">
    <loading v-if="false"></loading>
    <div class="content">
      <mt-header class="bar-nav" title="注册" >
        <mt-button slot="left" icon="back" v-back-link></mt-button>
        <router-link to="/login" slot="right">
          <mt-button class="main-color">登录</mt-button>
        </router-link>
      </mt-header>
      <div class="form-area">
        <form id="form_reg">
          <ul>
            <li class="margin-t-20">
              <rd-field type="tel" v-model="reg.mobile" label="手机号码" placeholder="请输入手机号码" name="mobile"></rd-field>
            </li>
            <li>
              <rd-field type="tel" v-model="reg.code" :attr="{maxlength: 6}" label="验证码" placeholder="请输入手机验证码" name="code">
                <button v-if="!reg.mobile" class="yzm color-999 aui-border-l ellipsis" disabled>发送验证码</button>
                <button v-else @click.prevent="getCode" class="yzm main-color aui-border-l ellipsis">发送验证码</button>
              </rd-field>
            </li>
            <li>
              <rd-field :type="changeType" v-model="reg.pwdVal"  label="登录密码" placeholder="设置登录密码 8-16位数字及字母组成" specPwd name="pwd">
                <img v-show="changeType != 'text'" src="../../assets/images/logReg/login_icon_show_pwd.png" class="show-hide-pwd" @click="hideShowPwd" data-flag="0">
                <img v-show="changeType == 'text'" src="../../assets/images/logReg/login_icon_hide_pwd.png" class="show-hide-pwd" @click="hideShowPwd" data-flag="1">
              </rd-field>
            </li>
            <li>
              <rd-field type="tel"  v-model="inviter" label="邀请人" placeholder="请输入邀请人手机号(选填)" name="inviter"></rd-field>
            </li>
          </ul>
          <div class="btn-con">
            <div class="protocol-box">
            <span @click="switchImg">
                <span v-if="switch_status">
                  <img src="../../assets/images/public/protocol_s.png" />
                </span>
                <span v-else>
                  <img  src="../../assets/images/public/protocol_n.png" />
                </span>
            </span>
              我已阅读并同意<a class="protocol main-color">《注册服务协议》</a></div>
            <mt-button v-if="reg.mobile && reg.code && reg.pwdVal" @click.native.prevent="checkForm" type="danger" size="large">
              <loading v-if="submitData" :task="false"></loading>
              <span v-else>注册</span>
            </mt-button>
            <mt-button v-else type="default" size="large" disabled>注册</mt-button>
          </div>
        </form>
      </div>
    </div>
  </div>

</template>

<script>
  import Loading from '../../components/Loading.vue'
  import RdField from '../../components/FieldInput.vue'
  import validator from '../../mixins/formValidator'
  import getCodeTime from '../../mixins/getCodeTime'
  import base64code from '../../mixins/base64code'
  import * as ajaxUrl from '../../ajax.config'
  import qs from 'qs'
  export default {
    data(){
      return {
        active: false,
        switch_status: true,
        submitData: false, //注册数据是否提交
        submitFlag: true, //防重复提交
        reg: {
          mobile: '',
          code: '',
          pwdVal: '',
          inviter: '',
          agree: 1
        },
        showHide: true,
        changeType: 'password'
      }
    },
    computed: {
      agree(){
        return this.switch_status ? 1 : 0;
      }
    },
    mixins: [validator, getCodeTime, base64code],
    methods: {
      switchImg () {
        this.switch_status = !this.switch_status
      },
      hideShowPwd(e){
        let flag = e.target.dataset.flag
        if(flag == '1'){
          this.changeType = 'password'
        }else{
          this.changeType = 'text'
        }
      },
      getCode (evt) {
        if(this.regexPhone(this.reg.mobile)){
          let params = {'mobile': this.reg.mobile}
          this.$http.get(ajaxUrl.registerPhoneCode, {params: {'mobile': this.reg.mobile}}).then((res) => {
            if(res.data.resMsg == '验证码已发送') {
              this.getCodeTime(evt)
            }else{
              this.$toast(res.data.resMsg)
            }
          })
        }
      },
      checkForm: function (e) {
        if(this.regexPhone(this.reg.mobile) && this.regexPwd(this.reg.pwdVal)){
          if(!this.agree){
            this.$toast('请勾选服务协议')
            return ;
          }
          if(!this.submitFlag){
            return ;
          }
          this.submitFlag = false; // 防重复提交
          this.submitData = true;
          let pwd = this.base64Encode(this.reg.pwdVal)
          let submitParams = Object.assign(this.reg, {pwd: pwd})
          let self = this
          this.$http.post(ajaxUrl.registerFirst, qs.stringify(submitParams)).then(function(res){
            if(res.data.resData != ''){
              let param = {
                code: self.reg.code,
                __sid: res.data.resData.__sid
              }
              // 第二步提交code和__sid
              self.$http.post(ajaxUrl.doRegister, qs.stringify(param)).then(function(res){
                if(res.data.resData != ''){
                  let user = {
                    username: res.data.resData.username,
                    userId: res.data.resData.userId,
                    avatarPhoto: res.data.resData.avatarPhoto,
                    expiresIn: res.data.resData.expiresIn,
                    __sid: res.data.resData.__sid
                  }
                  // 存储user信息到本地
                  sessionStorage.user = JSON.stringify(user)
                  self.$store.dispatch('login', user)
                  self.$router.push('/register/regSucc')
                }else{
                  self.submitData = false;
                  self.submitFlag = true;
                  self.$toast(res.data.resMsg)
                }
              })
            }else{
              self.submitData = false;
              self.submitFlag = true;
              self.$toast(res.data.resMsg)
            }
          })
        }
      }
    },
    components: { Loading, RdField }
  }
</script>

<style scoped>
  .bar-nav .title {  font-size:18px; }
  .btn-con {padding:0 .15rem;margin-top: .35rem;}
  .protocol-box img {width: .14rem;  vertical-align: -2px;}
  .form-area ul li .yzm {width: .9rem;}
  .show-hide-pwd {width: .2rem;margin: .1rem 0 0 0;position: absolute;top:.05rem; right:.12rem;}
</style>



